export default {
	onLoad(option) {
		this.id = option.id;
		console.log(this.id);
		this.getData();
	},
    data() {
        return {
			id:',',
			dataList:[],
			photo:[],
			spot:{},
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500
        }
    },
	methods:{
		getData(){
			let url = 'http://yxx.h-etrip.com:9092/etrip/api/app/et/spot/inst/' + this.id;
			uni.request({
			    url: url,
			    success: (res) => {
			        this.dataList = res.data.content;
					this.spot = res.data.content.spot;
					this.photo = res.data.content.photo;
					console.log(this.photo);
			    }
			});
		},
		getImgUrl(data) {
		     return this.getSrc(data);
		},
	}
}
